.div1 {
    background-color: #fbfbfb; 
}

.imgContainer {
    background: linear-gradient(to bottom, rgba(0,0,0,0.1),rgba(0,0,0,0.7)),url('../Images/C809C815-37EA-48FB-B898-E85D21D6BCEF.png');
    max-width: 100%;
    height: 480px;
    background-position-y: center;
    overflow: auto;
}

.logo {
    font-size: 70px;
    width: 147px;
    height: 147px;
    background-color: white;
    color: #eb2929;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    text-align: center;
    border-radius: 50%;
    margin-top: 81px;    
    padding-top: 16px;
}

.caption {
    margin-top: 22px;
    font-size: 36px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    color: white;
}

.inputs {
    margin-top: 38px; 
}

.dropdown1 {
    margin-left: 30%;
    width: 245px;
    height: 60px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 15px;
    color: #636f88;
    padding: 18px 20px 18px 18px;
}

.textbox {
    height: 60px;
    width: 477px;
    margin-left: 21px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 15px;
    color: #636f88;
    padding: 21px 100px 18px 60px;
}

@media only screen and (max-width: 1100px) { 
    
    .logo {
        font-size: 42px;
        width: 90px;
        height: 90px;
        padding-top: 10px;  
    }

    .caption {
        font-size: 28px;
    }
 }
 
@media only screen and (max-width: 1500px) { 
    .dropdown1 {
        margin-left: 25%;   
    }
 }

 @media only screen and (max-width: 1200px) { 
    .dropdown1 {
        margin-left: 20%;   
    }
 }

 @media only screen and (max-width: 992px) { 
    .dropdown1 {
        margin-left: 25%;   
        width: 477px;
    }

    .textbox {
        margin-top: 20px;
        margin-left: 25%;
    }
 }

 @media only screen and (max-width: 767.98px) { 

    .dropdown1 {
        margin-left: 15%;   
    }

    .textbox {
        margin-left: 15%;
    }  
 }

 @media only screen and (max-width: 576px) { 

    .dropdown1 {
        margin-left: 8%;
        width: 400px;   
    }

    .textbox {
        margin-left: 8%;
        width: 400px;
    }  

    .caption {
        font-size: 26px;
    }
 }
