.filter {
    display: flex;
    flex-direction: column;
    width: 260px;
    height: 700px;
    background-color: white;
    margin-left: 130px;
}

.heading {
    margin-left: 113px;
    font-family: 'Poppins', sans-serif;
    font-size: 36px;
    color: #192f60;
    margin-top: 37px;
    text-align: left;
    font-weight: 700;
}

.hotel1 {
    height: 291px;
}

.img1 {
    width: 120px;
    height: 120px;
    border-radius: 20%;
}

.tileText1{
    width:50%;
    float: left;   
}

.tileHeading1 {
    margin-top: 39px;
    margin-left: 41px;
    font-size: 30px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    color: #192f60;
}

.tileSubHeading {
    font-size: 16px;
    margin-left: 46px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    color: #636f88;
}

.fort {
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    color: #192f60;
}

.filterSort {
    margin-left: 27px;
}

.filterHeading, .sort {
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    font-weight: 600;
    color: #192f60;
}

.cuisine1, .costfortwo1, .selectOption {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #192f60;
}

.cuisineCost {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #8c96ab;
}

.filter1 {
    display: none;
}

.order {
    font-weight: 600;
    color: #192f60;
    font-size: 30px;
    font-family: 'Poppins', sans-serif;
}

.cuisines2 {
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    color: #636f88;
}

.cuisines3 {
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    color: #192f60;
}

.filterbutton {
    margin-left: 115px;
    width: 300px;
    display: none;
}

@media only screen and (max-width: 768px) {

    .heading {
        margin-left: 50px;
        font-size: 30px;
    }

}

@media only screen and (max-width: 1025px) {
   .tileHeading1 {
       font-size: 25px;
   }

   .tileSubHeading {
        font-size: 14px;
   }

   
}

@media only screen and (max-width: 1200px) {
    .tileHeading1 {
        margin-top:15px;
    }

    .filterbutton {
        display: block;
     }
 }