.bodyContainer {
    height: 1441px;
    background-color: #fbfbfb;
    overflow: auto;
}

.imageContainer{
    position: relative;
    width: 1130px;
    height: 450px;
    background-repeat: no-repeat;
    background-position-y: -130px;
    margin-top: 52px;
}

.gallery {
    position:absolute;
    right: 21%;
    top: 520px;
    width: 242px;
    height: 48px;
    background-color: rgba(255, 255, 255, 0.8);
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    border-radius: 3px;
    text-align: center;
    padding-top: 13px;
}

@media only screen and (max-width: 1500px) {
    .gallery {
        right: 12%; 
    }
}

@media only screen and (max-width: 1200px) {
    .imageContainer{
        width: 100%;
    }

}

@media only screen and (max-width: 992px) {
    .imageContainer{
        margin-top: 20px;
        margin-left: 0px;
        height: 300px;
    }


    .gallery {
        width: 235px;
        height:42px;
        font-size: 14px;
        margin-top: -15px;
        top:360px;
    }
}

