.header {
    overflow: auto;
    background-color: #ce0505;
    height: 80px;
}

.logo1 {
    color: #ce0505;
    background-color: white;
    width: 52px;
    height: 52px;
    margin-left: 102px;
    margin-top: 15px;
    font-size: 33px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    text-align: center;
    border-radius: 50%;
}

.createAccount {
    color: white;
    border: 1px solid white;
    height: 46px;
    font-size: 16px;
    margin-top: 17px;
    margin-right: 101px;
    margin-left: 26px;
    padding: 12px 14px 11px 15px;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    border-radius: 3px;
}

.login{
    color: white;
    cursor: pointer;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    margin-top: 29px;
}

@media only screen and (max-width: 768px) {
    .logo1 {
        margin-left: 30px;
        width: 45px;
        height: 45px;
        font-size: 30px;
    }

    .login {
        font-size: 14px;
    }

    .createAccount {
        margin-right: 30px;
        height: 40px;
        font-size: 14px;
    }
}